export default {
	data() {
		return {
			name:'请点击登录',
			avatarUrl:'',
			phon:"手机号未绑定",
			phone_dian:"",
			roles:"",
			role:false
		}
	},
	onLoad() {
		this.addOusrs()
		const message = uni.getStorageSync("coken")
		this.name = message.nick
		this.avatarUrl = message.avatarUrl
		this.conIndex()
	},
	onShareAppMessage( options ){
	  　　var that = this;
	  　　// 设置菜单中的转发按钮触发转发事件时的转发内容
	  　　var shareObj = {
	  　　　　title: "雀胜",        // 默认是小程序的名称(可以写slogan等)
	  　　　　path: '/pages/ourselves/ourselves',        // 默认是当前页面，必须是以‘/’开头的完整路径
	  　　　　imageUrl: '',     //自定义图片路径，可以是本地文件路径、代码包文件路径或者网络图片路径，支持PNG及JPG，不传入 imageUrl 则使用默认截图。显示图片长宽比是 5:4
	  　　　　success: function(res){
	  　　　　　　// 转发成功之后的回调
	  　　　　　　if(res.errMsg == 'shareAppMessage:ok'){
	  　　　　　　}
	  　　　　},
	  　　　　fail: function(){
	  　　　　　　// 转发失败之后的回调
	  　　　　　　if(res.errMsg == 'shareAppMessage:fail cancel'){
	  　　　　　　　　// 用户取消转发
	  　　　　　　}else if(res.errMsg == 'shareAppMessage:fail'){
	  　　　　　　　　// 转发失败，其中 detail message 为详细失败信息
	  　　　　　　}
	  　　　　}
	  　　};
	  　　return shareObj;
	  },
	onPullDownRefresh() {
	    this.refresh()
	    },
	onShow() {
		const message = uni.getStorageSync("coken")
		this.name = message.nick
		this.avatarUrl = message.avatarUrl
	    this.shouJi()
		// this.addOusrs()
		this.conIndex()
	},
	methods: {
		about(){
			uni.navigateTo({
				url:"../about_my/about_my"
			})
		},
		kefu(){
			uni.makePhoneCall({
				phoneNumber:this.phone_dian
			})
		},
		xiaoxi(){
			uni.navigateTo({
				url:"../my_news/my_news"
			})
		},
		addOusrs(){
		    const token = uni.getStorageSync("token")
			if(!token) {
				uni.navigateTo({
					url:"../login/login"
				})
			}
		},
		addPhone(){
			const phone = uni.getStorageSync("phone")
			if(!phone){
				uni.navigateTo({
					url:"../huo_phone/huo_phone"
				})
			}
		},
	    async conIndex(id){
	        const that = this
	            const token = uni.getStorageSync("token")
	                if(token != '') {
	                    that.uid = token.uid
	                }
	            const res = await this.$myRequest({
	                method:"POST",
	                url:"/getphone",
	                data:{
	                    uid:that.uid
	                }
	            })
				that.phone_dian = res.data.data.phone
	            if(res.data.data.userinfo != null){
					that.roles = res.data.data.userinfo.roles
					if(that.roles == 0){
						that.role = false
					}else{
						that.role = true
					}
				}
	        },
		async shouJi(){
			const that = this
			const token = uni.getStorageSync("token")
			const uid = token.uid
			const res = await this.$myRequest({
				method:"POST",
				url:"/checkMobile",
				data:{
					uid:uid
				},
			})
			if(res.data.code == 1){
				that.phon = res.data.data
			}
		},
		refresh(){
			setTimeout(()=>{
				this.postIndex()
				uni.stopPullDownRefresh()
			},1000)
		},
		ma_order(id){
			uni.navigateTo({
				url:"../my_order/my_order?id=" + this.roles
			})
		},
		myMessage(){
			uni.navigateTo({
				url:"../login/login"
			})
		},
		myPhone(){
			uni.navigateTo({
				url:"../huo_phone/huo_phone"
			})
		}
	}
}
